@use "../../../global_variables" as _;

.main {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 100%;
    & > .header {
        display: flex;
        align-items: center;
        gap: 10px;
        width: min(95%, 720px);
        margin: 10px 0;
        & > h1 {
            font-size: 1.25 * _.$base-font-size;
            font-weight: normal;
        }
    }
}
